import React from "react";
import PropTypes from "prop-types";

import { DropTarget } from "react-dnd";
import ItemTypes from "../types";

import Views from './Views'
import AppViews from './AppViews'

import "./ViewLayout.css";

import {Button} from 'react-bootstrap'

import BannerView from './BannerView'
import TimeView from './TimeView'
import CouponView from './CouponView'
import CommidityView from './CommidityView'
import FlashSaleView from './FlashSaleView'
import AwardView from './AwardView'

const List = require("../../listData/viewData.json"); //
const editsList = require("../../listData/editData.json");

const boxTarget = {
	drop: () => ({ name: "Dustbin" })
};

@DropTarget(
	ItemTypes.TEST,
	boxTarget,
	(connect, monitor) => ({
		connectDropTarget: connect.dropTarget(),
		isOver: monitor.isOver(),
		canDrop: monitor.canDrop(),
		dropResult: monitor.getDropResult(),

		dropItemType: monitor.getItemType(),
		dropItem: monitor.getItem(),

		didDrop: monitor.didDrop(),
		initialClientOffset: monitor.getInitialClientOffset(),
		initialSourceClientOffset: monitor.getInitialSourceClientOffset(),
		clientOffset: monitor.getClientOffset(),
		differenceFromInitialOffset: monitor.getDifferenceFromInitialOffset(),
		sourceClientOffset: monitor.getSourceClientOffset()
	})
)


class  ViewLayout extends React.Component {
	static propTypes = {
		canDrop: PropTypes.bool.isRequired,
		isOver: PropTypes.bool.isRequired,
		connectDropTarget: PropTypes.func.isRequired
	};

	constructor(props) {
		super(props) 
		this.state = {
			modal: 'web', //
			List: List,
			controls: {
				bannerControlEdits: {}, //banner
				timeControlEdits: {}, //
				couponControlEdits: {}, //
				commidityControlEdits: {}, //
				flashSaleControlEdits: {}, //
				awardControlEdits: {} //
			} //
		}
	} 

	componentWillMount() {
		this.setState(old => {
			old.List = window._.getControls()
			return old
		})
	}

	//
	handleChangeViewModal(name) {
		this.setState(old => {
			old.modal = name
			return old
		})
	} 

	//
	handleMoveOn(index) {
		console.log("index", index)
		window._.moveOnControl(index)
		this.forceUpdate()
	}

	//
	handleMoveDown(index) {
		console.log("index", index)
		window._.moveDownControl(index)
		this.forceUpdate()
	}

	//
	handleDelete(index) {
		window._.deleteControl(index) //
		this.forceUpdate()
	}

	//
	listRender(item, i) {
		console.log("item", item )
		const {controls, modal} = this.props
		const List = window._.getControls()
		const Components = [
			{
				componentName: BannerView, //
				name: 'banner'
			},{
				componentName: TimeView,
				name: 'time'
			},{
				componentName: CouponView,
				name: 'coupon'
			},{
				componentName: CommidityView,
				name: 'commidity'
			},{
				componentName: FlashSaleView,
				name: 'falshSale'                
			},{
				componentName: AwardView,
				name: 'award'
			},
		]
		const ComponentName = Components[item.funcIndex].componentName // 
		// // 
		//react
		return  <ComponentName 
					name= {Components[item.funcIndex].name}
					list = {List}
					modal={this.state.modal} 
					onClick={this.props.onClick} 
					index={i} 
					length={List.length} //
					moveOn={this.handleMoveOn.bind(this)}
					moveDown={this.handleMoveDown.bind(this)}
					ondelete={this.handleDelete.bind(this)}
					item={item}
					key={i}
				/>;
	}
	


	render() {
		const {canDrop, isOver, connectDropTarget} = this.props;
		const isActive = canDrop && isOver;

		//
		const contentBoxStyle = {
			border: isActive ? "1px dashed #222" : "1px dashed #fff",
			width:  this.state.modal == 'web' ? '95%' : '40%',
			margin: "0 auto",
			minHeight: "30%"
		};

		const List = window._.getControls() //
		return (
			connectDropTarget && connectDropTarget(
				<div
					className="container"
					style={{ width: '65%', backgroundColor: "#ddd", padding: "20px", marginLeft: '10%', minHeight: '1000px', overflowY: 'scroll' }}
				>
					<div style={{position: 'relative', marginBottom: '30px'}}>
						<h3 className="header" style={{ paddingLeft: "20px", color: "#fff" }}>
							
						</h3>
						<div style={{position: 'absolute', right: '5%', top: '0px'}}>
							<Button variant="info" onClick={this.handleChangeViewModal.bind(this, 'web')}>web</Button>{' '}{' '}{' '}
							<Button variant="info" onClick={this.handleChangeViewModal.bind(this, 'app')}>App</Button>
						</div>
					</div>
				
					<div className="contentBox" style={contentBoxStyle} >
						{
							List.length === 0 ? 
							<p
								style={{
								fontSize: "28px",
								color: "#fff",
								textAlign: "center",
								verticalAlign: "center"
								}}
							>
								~
							</p>
							: 
							!!List && List.map((item, key) => {
								console.log("item", item)
								return this.listRender(item, key) //
							})
						}
				</div>
			</div>)
		);
	}
}

export default ViewLayout;
