import React, { Component } from 'react'

import {Image} from 'react-bootstrap'
import P1 from './images/p1.jpg'
import OperationBox from './OperationBox'
import SlideShow from './SlideShow'



class BannerView extends Component {
	constructor(props) {
		super(props)
		this.state = {
			hover: false,
			BannerControl: {
				isfloor: false,
				floorName: '', 
				picStyle: 1, // 01
				picMaxnum4Web: 1,
				picMaxnum4App: 1,
				backgroundColor: '#ffffff',
				bannerProperty: []
			}
		}
	}

	handleHover() {
		this.setState(old => {
			old.hover = true
			return old
		})
	}

	handleLeave() {
		this.setState(old => {
			old.hover = false
			return old
		})
	}

	//banner
	// colRender(colNum4Web) {
	// 	let output = []
	// 	for(let i = 0; i < colNum4Web; i++) {
	// 		output.push(
	// 			<div style={{width: `calc(100% / ${colNum4Web})`}}>
	// 				<Image 
	// 					src={P1} 
	// 					rounded  
	// 					style={{height: this.props.modal === 'web' ? '200px' : '150px', position: 'relative', width: '100%'}} 
	// 				/>
	// 			</div>
	// 		)
	// 	}
	// 	return output 	
	// }

	//webbanner
	tileBannerRender(item) {
		const colNum = this.props.modal === 'web' ? item.picMaxnum4Web : item.picMaxnum4App//web
		const len = item.bannerProperty.length || 1 //banner //

		let output = []
		for(let i = 0; i < len; i++) {
			output.push(
				<span style={{width: `calc(100% / ${colNum})`, display: 'inline-block'}}>
					<Image 
						src={P1} 
						rounded  
						style={{height: this.props.modal === 'web' ? '200px' : '150px', position: 'relative', width: '100%'}} 
					/>
				</span>
			)
		}
		return output 	
	}

	bannerRender(data) {
		const output = +data.picStyle ?  //
		<div style={{width: '100%'}}>
			{this.tileBannerRender(data)} 
		</div>
		: 
		<SlideShow /> //appweb
		return output
	}

	//

	// buttonRender() {
	// 	//const {index, length, moveOn, moveDown, ondelete} = this.props
	// 	const {index, length, moveOn, moveDown, ondelete, item} = this.props
    //     console.log("index", index)
    //     let output
    //     length === 1 ?
    //     output = <Button variant="danger" onClick={ondelete.bind(this, index)}></Button>
    //     :
    //     output = <div>
    //                 {
    //                     length != 1 && index != 0 ? 
    //                     <Button variant="info" onClick={moveOn.bind(this, index)} style={{marginRight: '5px'}}></Button> : null 
    //                 }
    //                 {
    //                     length != 1 && index != length - 1 ? 
    //                     <Button variant="info" onClick={moveDown.bind(this, index)} style={{marginRight: '5px'}}></Button> : null 
    //                 }
    //                 <Button variant="danger" onClick={ondelete.bind(this, index)}></Button>
    //             </div>
    //     return output
    // }

	render() {
		const {index, length, moveOn, moveDown, ondelete} = this.props
		const data = window._.getControls()[index].BannerControl //banner
		const {hover} = this.state

		return(
			<div 
				onClick={this.props.onClick.bind(this, 'banner', 0, index)} 
				style={{marginBottom: '10px', width: '100%', backgroundColor: `${data.backgroundColor}`}} 
				onMouseEnter={this.handleHover.bind(this)} 
				onMouseLeave={this.handleLeave.bind(this)}
			>
				<div style={{position: 'relative', width: '100%'}}>
					{this.bannerRender(data)}
					<div style={{position: 'absolute', top: '0px', right: '0px', visibility: hover ? 'visible' : 'hidden'}}>
						{
							<OperationBox 
								index={index}
								length={length}
								moveOn={moveOn}
								moveDown={moveDown}
								ondelete={ondelete}
							/>
						}	
                    </div>
				</div>
			</div>
		)
    }
}

export default BannerView

