import React, { Component } from 'react'

import {Form, Button} from 'react-bootstrap'

export default class extends Component {

    constructor(props) {
        super(props)
        this.state = {
            awardControl: {
                isFloor: false,
                floorName: '',
                awardName: '',
                disTimeRange: {
                    startTime: '',
                    endTime: ''
                }
            }
        }
    }

    handleChange() {
        
    }


    render() {
        
        return (
            <div  style={{width: '95%', margin: '20px auto', padding: '10px'}}>
                <div style={{fontSize: '20px', fontWeight: '600', marginBottom: '20px'}}></div>
                <Form>
                    <Form.Group>
                        <Form.Label>: <span style={{color: 'red'}}>*</span></Form.Label>
                        <Form.Control as="select">
                            <option></option>
                            <option></option>
                        </Form.Control>
                    </Form.Group>
                    <Form.Group>
                        <Form.Label>: <span style={{color: 'red'}}>*</span></Form.Label>
                        <Form.Control as="select">
                            <option></option>
                            <option></option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group>
                        <Form.Label>Web: <span style={{color: 'red'}}>*</span></Form.Label>
                        <Form.Control as="select">
                            <option></option>
                            <option>1</option>
                            <option>2</option>
                            <option>3</option>
                            <option>4</option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group>
                        <Form.Label>App: <span style={{color: 'red'}}>*</span></Form.Label>
                        <Form.Control as="select">
                            <option></option>
                            <option>1</option>
                            <option>2</option>
                            <option>3</option>
                            <option>4</option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group controlId="exampleForm.ControlTextarea1">
                        <Form.Label> <span style={{color: 'red'}}>*</span></Form.Label>
                        <Form.Control as="input" />
                    </Form.Group>
                    
                    {
                        this.state.bannerConfigOptions.map((item, index) => {
                            return item;
                        })
                    }
                    
                </Form>
            </div>
        )
    }
}
