import React, { Component } from 'react'
import './slideShow.css'

export default class SlideShow extends Component {
    constructor() {
        super();
        this.state = {
            imgs: [
                './images/p1.jpg',
                './images/p2.jpg',
                './images/p3.jpg'
            ],
            showIndex: 0,
            timer: null,
            show: false
        }
    }

    componentDidMount(){ //
        this.start();
    }
    componentWillUnmount() { //
        this.stop();
    }
    stop = () => { //
        let {timer} = this.state;
        clearInterval(timer);
    }
    start = () => { //
        let {timer} = this.state;
        timer = setInterval(() => {
            this.next();
        }, 2000);
        this.setState({
            timer
        })
    }
    change = (index) => { //
        let {showIndex} = this.state;
        showIndex = index;
        this.setState({
            showIndex
        })
    }
    previous = (e) => { //
        let ev = e || window.event;
        let {showIndex, imgs} = this.state;
        if(showIndex <= 0){
            showIndex = imgs.length - 1;
        }else{
            showIndex --;
        }
        this.setState({
            showIndex
        })
    }
    next = (e) => { //
        let ev = e || window.event;
        let {showIndex, imgs} = this.state;
        if(showIndex >= imgs.length - 1){
            showIndex = 0;
        }else{
            showIndex ++;
        }
        this.setState({
            showIndex
        })
    }

    render() {
        return (
            <div className="ReactCarousel">
                <div className="contain" 
                     onMouseEnter={()=>{this.stop()}} //
                     onMouseLeave={()=>{this.start()}}  //
                >
                    <ul className="ul">
                        {
                            this.state.imgs.map((value, index) => {
                                return (
                                    <li className={index === this.state.showIndex ? 'show' : ''}
                                        key={index} 
                                    > 
                                        <img src={require(value + '')} alt="" style={{width: '100%'}}/>
                                    </li>
                                )
                            })
                        }
                    </ul>
                    <ul className="dots"> 
                        {
                            this.state.imgs.map((value, index) => {
                                return (
                                    <li key={index}  
                                        className={index === this.state.showIndex ? 'active' : ''} 
                                        onClick={()=>{this.change(index)}}>
                                    </li>)
                            })
                        }
                        
                    </ul>
                </div>
            </div>
        )
    }
}
