import React, { Component } from 'react'

import {Form, Button} from 'react-bootstrap'

export default class extends Component {

    constructor(props) {
        super(props)
        this.state = {
            timeControl: {
                isFloor: false,
                floorName: '',
                countDownText: '',
                countDownTime: '',
                backgroundColor4Web: '',
                img4Web: ''
            }
        }
    }

    handleChange(e) {
        const {index} = this.props
        console.log("index", index)
        console.log("e.target.value", e.target.value)
        const name = e.target.name
        const value = e.target.value
        window._.changeAttribute(1, 'TimeControl', name, value) 
    }
    render() {
        const {key, index, item, type} = this.props
        console.log("index", index)
        return (
            <div style={{width: '95%', margin: '20px auto', padding: '10px'}}>
                <div style={{fontSize: '20px', fontWeight: '600', marginBottom: '20px'}}></div>
                <Form>
                    <Form.Group>
                        <Form.Label>: <span style={{color: 'red'}}>*</span></Form.Label>
                        <Form.Control as="select">
                            <option></option>
                            <option></option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group>
                        <Form.Label>: <span style={{color: 'red'}}>*</span></Form.Label>
                        {
                            <Form.Control type="input" name="countDownText" onChange={(e) => this.props.onChange(e, index, 'TimeControl')} />
                        }
                    </Form.Group>

                    <Form.Group>
                        <Form.Label>: <span style={{color: 'red'}}>*</span></Form.Label>
                        <Form.Control as="input" />
                    </Form.Group>

                    <Form.Group controlId="exampleForm.ControlTextarea1">
                        <Form.Label>Web <span style={{color: 'red'}}>*</span></Form.Label>
                        <Form.Control as="input" />
                    </Form.Group>

                    <Form.Group>
                        <Form.Label>web: <span style={{color: 'red'}}>*</span></Form.Label>
                        <Form.Control as="input" />
                    </Form.Group>
                    
                </Form>
            </div>
        )
    }
}
