	handleAddChildEdit(e, index) {
		//
		console.log("list", window._.getControls(), index) //list
		let list = window._.getControls()
		let arr = list[index].BannerControl.bannerProperty; //
		const name = e.target.name //name
        arr.push({
            bannerSortIndex: '',
            bannerImg4Web: '',
            bannerLinkType4Web: '',
            bannerImg4App: '',
            bannerLinkType4App: ''
		});
		list[index].BannerControl.bannerProperty = arr
		window._.changeControl(index, list[index])
		this.init()
	}
	
	handleRemoveChildEdit(e, index, controlIndex) { 
		//index
		console.log("list", this.state.list, index, controlIndex)
		const name = e.target.name
        let arr = this.state.list[controlIndex].BannerControl.bannerProperty; //
        arr.splice(index, 1)
        this.setState(old => {
			old.list[controlIndex].BannerControl.bannerProperty = arr
            return old
		})
		window._.changeControl(controlIndex, this.state.list[controlIndex])
		this.init()
    }

	// //
	handleChangeControls(e, index, controlname) {
        console.log("index", index)
        console.log("e.target.value", e.target.value)
        const name = e.target.name
        const value = e.target.value
        //indexindex
        //index, controlname, name, value
		window._.changeAttribute(index, controlname, name, value) //
		this.forceUpdate()
	}

